import Router from "next/router";
import NProgress from "nprogress";
import Link from "next/link";
import Icon from "../icons";

import "./Nav.scss";

Router.onRouteChangeStart = () => {
  NProgress.start();
};

Router.onRouteChangeComplete = () => {
  NProgress.done();
};

Router.onRouteChangeError = () => {
  NProgress.done();
};

const Nav = () => (
  <header className="user-nav__header">
    <div className="user-nav__logo">
      <Link href="/">
        <a>
          <img
            src="https://res.cloudinary.com/dk9vbfmw6/image/upload/v1549146635/circlegps-logo-nobg.png"
            alt="Logo"
            class="user-nav__logo-image"
          />
        </a>
      </Link>
    </div>
    <nav className="user-nav">
      <Link to="/add-device" className="user-nav__icon-box">
        <a>
          <Icon className="user-nav__icon" name="cog" />
          <span className="user-nav__settings">Settings</span>
        </a>
      </Link>
      <Link to="/" className="user-nav__icon-box">
        <a>
          <Icon className="user-nav__icon" name="lock" />
          <span className="user-nav__user-name">Logout</span>
        </a>
      </Link>
    </nav>
  </header>
);

export default Nav;
